.chessboard {
    margin-bottom: auto;
    width: 80vmin;
    aspect-ratio: 1/1;
    background-image: url('../../assets/chessboard.png');
    background-size: 100% 100%;
    position: relative;
    border-radius: 3px;
}

.chessboard-sidebar {
    flex: 0 0 40px;
}

.pov-button {
    width: 100%;
    aspect-ratio: 1/1;
}

.chessboard-layout {
    margin: auto;
    margin-left: 45px;
    justify-content: center;
    flex: 0 1 auto;
    display: flex;
}

@media (max-aspect-ratio: 4/3) {
    .chessboard-layout {
        flex: 0 1 auto;
        margin: 20px;
    }
}